import Head from 'next/head'
import linkDataMisc from '../components/linkDataMisc'
import Links from '../components/Links'
import MyNav from "../components/MyNav"

function misc() {
    const linkComponents = linkDataMisc.map(link => <Links key={link.id} site={link.site} url={link.url} />)
    return (
        <div className="text-center">
            <Head>
                <title>Course Services Links</title>
            </Head>
            <MyNav />
            <h1>
                Course Services Resources
        </h1>


            <h2>Misc Links</h2>
            {linkComponents}
            <Links />
        </div>
    )
}

export default misc